
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Poppins:wght@600&display=swap');
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Open sans', sans-serif;
  font-size: 18px;
  margin: 0;
}
h1,
h2 {
  font-family: 'Poppins', sans-serif;
}
.container {
  width: 1200px;
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
p {
  opacity: 0.6;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
footer {
  background-color: #0278ae;
  color: #fff;
  padding: 20px ;
}
footer ul {
  list-style-type: none;
}
footer ul li {
  margin-bottom: 20px;
}
.align-start {
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  margin-top: 30px;
}
.icons {
  color: #fff;
}
.icon-location {
  position: relative;
  top: 5px;
}
.icon-phone {
  position: relative;
  top: 55px;
}
.icon-email {
  position: relative;
  top: 50px;
}
.pa {
  width: 40%;
  margin-left: 0;
  margin-top:55px;
}
.circle {
  border: 1px solid #fff;
  border-radius: 50%;
  color: #fff;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  text-decoration: none;
}
.circle:hover {
  color: hsl(322, 100%, 66%);
  border: 1px solid hsl(322, 100%, 66%);
  -webkit-transition: 1s;
  transition: 1s;
}
footer p {
  text-align: right;
  font-size: 10px;
}
footer p a {
  text-decoration: none;
}
#footer {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 300px; /* Height of the footer */
}





