
@import url("https://fonts.googleapis.com/css?family=Work+Sans");
body {
  font-family: "Work Sans", sans-serif;
  background: #00d2ff;
  background: -webkit-gradient(linear, left top, right top, from(#fff), to(#fff));
  background: linear-gradient(to right, #fff, #fff);
}
.card {
  background: #16181a;
  border-radius: 14px;
  max-width: 300px;
  display: block;
  margin: auto;
  padding: 60px;
  padding-left: 20px;
  padding-right: 20px;
  -webkit-box-shadow: 2px 10px 40px black;
          box-shadow: 2px 10px 40px black;
  z-index: 99;
}
.logo-card {
  max-width: 50px;
  margin-bottom: 15px;
  margin-top: -19px;
}
label {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 10px;
  color: white;
  opacity: 0.4;
}
.CCN {
  padding-right: 50px;
  font-size: 20px;
  font-family: "Work Sans", sans-serif;
  background: transparent;
  border: none;
  color: #dbdce09d;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}
select:focus {
  border-bottom: 1px solid #1abc9c;
  outline: none;
}
.delivery {
  background: #16181a00;
  border-radius: 6px;
  color: #000000;
  -webkit-box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
          box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
}
.return {
  border: none;
  margin-top: 60%;
  font-size: 20px;
  font-family: "Work Sans", sans-serif;
  background: #d42c2c1c;
  border-radius: 6px;
}
.return:hover {
  background: #d42c2c6b;
}
.cardnumber {
  padding-left: 13px;
  font-size: 15px;
  margin-bottom: 8px;
}
.name {
  display: block;
  font-size: 15px;
  max-width: 200px;
  float: left;
  margin-bottom: 15px;
}
.toleft {
  float: left;
}
.ccv {
  width: 50px;
  margin-top: -5px;
  font-size: 15px;
}
.receipt {
  background: #ffffff;
  border-radius: 4px;
  padding: 5%;
  padding-top: 250px;
  max-width: 600px;
  margin: auto;
  margin-top: -180px;
  color: #000000;
  -webkit-box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
          box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
}
.column {
  padding-left: 10%;
  text-align: left;
  float: left;
  width: 50%;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.cost {
  color: #3a7bd5;
}
.seller {
  color: #3a7bd5;
}
.description {
  font-size: 13px;
}
.price {
  font-size: 12px;
}
.comprobe {
  text-align: center;
}
.proceed {
  position: absolute;
  -webkit-transform: translate(300px, 10px);
          transform: translate(300px, 10px);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #1abc9c;
  border: none;
  color: white;
  -webkit-transition: -webkit-box-shadow 0.2s, -webkit-transform 0.4s;
  transition: -webkit-box-shadow 0.2s, -webkit-transform 0.4s;
  transition: box-shadow 0.2s, transform 0.4s;
  transition: box-shadow 0.2s, transform 0.4s, -webkit-box-shadow 0.2s, -webkit-transform 0.4s;
  cursor: pointer;
  -webkit-box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
          box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
}
.proceed:active {
  outline: none;
  -webkit-box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
          box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.288);
}
.proceed:focus {
  outline: none;
  -webkit-box-shadow: inset 0px 0px 5px white;
          box-shadow: inset 0px 0px 5px white;
}
.sendicon {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  padding-top: 2px;
}
@media (max-width: 600px) {
.proceed {
    -webkit-transform: translate(250px, 10px);
            transform: translate(250px, 10px);
}
.col {
    display: block;
    margin: auto;
    width: 100%;
    text-align: center;
}
}



